import React, {useEffect, useState} from "react";
import "../styles/navigation.css";
import {useLocation, useNavigate} from "react-router-dom";
import {Avatar, Button, Dropdown, Flex, Space, Typography} from "antd";
import {MoonOutlined, SunOutlined} from "@ant-design/icons";
import Header from "./Header";

export default function NavigationBar({currentRoute, onToggle, isDarkMode}) {
    const [burgerOn, setBurgerOn] = useState(false);
    const navigate = useNavigate();
    const items = [
        {key: "1", label: <button>Latest</button>},
        {key: "2", label: <button>Event</button>},
        {key: "3", label: <button>Page</button>},
    ];

    const {Title, Paragraph} = Typography;
    useEffect(() => {}, []);
    return (
        <nav>
            <Flex align='center'>
                <img src='logo_alpha.png' alt='' style={{height: "4rem"}} />
                <Title level={3}>ECOS</Title>
            </Flex>
            <Space>
                <Button type='text' onClick={() => navigate("/")}>
                    Home
                </Button>
                <Dropdown menu={{items}} placement='bottomCenter' arrow>
                    <Button type='text' onClick={() => navigate("/events")}>
                        Events
                    </Button>
                </Dropdown>
                <Dropdown menu={{items}} placement='bottomCenter' arrow>
                    <Button type='text'>Creation</Button>
                </Dropdown>
                <Button
                    shape='circle'
                    icon={isDarkMode ? <MoonOutlined /> : <SunOutlined />}
                    onClick={() => onToggle(!isDarkMode)}
                ></Button>
            </Space>
        </nav>
    );
}
