import React, {useContext} from "react";
import Header from "../components/Header";
import LatestEvent from "../components/LatestEvent";
import {Card, Carousel, Space, Typography} from "antd";
import {AppContext} from "../App";

const {Title, Paragraph} = Typography;

export default function HomePage() {
    return (
        <div className='page'>
            <Carousel arrows>
                <div className='hero'></div>
                <div className='hero'></div>
            </Carousel>
            <div className='about'>
                <Title level={2}>About Us</Title>
                <Paragraph>
                    English Club of SMK Negeri 3 Jepara is a club where we can
                    learn and earn English skills. We all know that smart isnt
                    naturally coming up, so lets study together and showing our
                    skills up.
                </Paragraph>
            </div>
            <Title level={2}>Why Join Us</Title>
            <div
                style={{
                    display: "grid",
                    gridTemplateColumns: "repeat(auto-fit, minmax(15rem, 1fr))",
                    gap: ".5rem",
                }}
            >
                {[1, 2, 3, 4, 5].map((item) => (
                    <Card title='why'>
                        Lorem ipsum dolor sit amet consectetur adipisicing elit.
                        Dolore modi a laborum laboriosam tenetur eligendi
                        accusantium, illum deserunt tempore est.
                    </Card>
                ))}
            </div>
            <LatestEvent />
        </div>
    );
}
