import {Flex, Space, Typography} from "antd";
import React from "react";

const {Title, Paragraph} = Typography;

export default function Header() {
    return (
        <div
            style={{
                display: "flex",
                alignItems: "center",
                fontSize: "1.4rem",
                background: "red",
            }}
        >
            <img src='logo_alpha.png' alt='' style={{height: "30%"}} />
            <Title level={2}>ECOS</Title>
        </div>
    );
}
