import React from "react";
import Event from "./Event";

export default function LatestEvent({events}) {
    return (
        <div className='event' style={{width: "100%"}}>
            <h2>Latest Event</h2>
            <div
                className='container'
                style={{display: "flex", gap: ".5rem", width: "100%"}}
            >
                {[1, 2, 3].map((item) => (
                    <Event />
                ))}
            </div>
        </div>
    );
}
